import React, { useState, useContext } from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import { Button, Grid, CircularProgress } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import styles from '../css/ConnectInfoModal.module.css';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import TextField from '@mui/material/TextField';
import { CommonContext } from '../CommonContext';

const ConnectInfoModal = (props) => {
    const [formData, setFormData] = useState({
        connectType: '',
        ipAddress: '',
        port: ''
    });

    //      .
    const [connecting, setConnecting] = useState(false);
    const [error, setError] = useState(null);

    const { DRONEKIT_API, setDRONEKIT_API } = useContext(CommonContext);

    const handleClose = () => {
        setFormData({
            connectType: '',
            ipAddress: '',
            port: ''
        });
        setConnecting(false); //    .
        setError(null); //   .
        props.setOpen(false);
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setConnecting(true); //      ' ' .
        setError(null); //    .

        if (!formData.connectType || !formData.ipAddress || !formData.port) {
            alert(", IP,  .");
            setConnecting(false); //       .
            return;
        }

        try {
            const response = await fetch(DRONEKIT_API + 'connect_drone', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    "connection_string": formData.connectType + ":" + formData.ipAddress + ":" + formData.port,
                }),
            });

            if (!response.ok) {
                throw new Error('Network response was not ok.');
            }

            const data = await response.json();
            console.log(data);
            if(data.status === "Connected") {
                alert(" .");
                handleClose();
                props.addDroneMonitor();
            }

            if(data.status === "Already Connected") {
                alert("  .");
                props.addDroneMonitor();
                handleClose();
            }

            if(data.status === "Failed") {
                setError(data.details); //   .
            }
        } catch (error) {
            console.error("Error:", error);
            setError(error); //   .
        } finally {
            setConnecting(false); //     '' .
        }
    };

    return (
        <Modal
            open={props.open}
            onClose={handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description">
            <Box className={styles.addDroneModal}>
                <Grid container style={{ background: "rgba(0,0,0,0.5)", height: "30px", alignItems: "center" }}>
                    <Grid item xs={11} style={{ color: "white", fontWeight: "bold", fontSize: "1.1em", opacity: "1", textAlign: "center" }}>
                          
                    </Grid>
                    <Grid item xs={1} style={{ textAlign: "right" }}>
                        <button onClick={handleClose} style={{ padding: "0", border: "0", marginRight: "5px", backgroundColor: "black" }}>
                            <CloseIcon style={{ color: "white", width: "20px", height: "20px" }} />
                        </button>
                    </Grid>
                </Grid>
                <Grid container style={{ background: "rgba(100,100,100,0.7)", alignItems: "center", padding: "20px" }}>
                    <Grid item xs={12} style={{textAlign:"center"}}>
                        {connecting ? (
                            //    CircularProgress .
                            <div>
                            <CircularProgress /><br/><span style={{color:"white"}}>...</span>
                            </div>
                        ) : (
                            //      .
                            <form onSubmit={handleSubmit}>
                                <FormControl fullWidth size="small">
                                    <InputLabel id="sel-connect-label"></InputLabel>
                                    <Select
                                        name="connectType"
                                        labelId="sel-connect-label"
                                        id="connectType"
                                        value={formData.connectType}
                                        label="Connect Type"
                                        onChange={handleChange}
                                        style={{ backgroundColor: "white" }}
                                    >
                                        <MenuItem value={"tcp"}>TCP</MenuItem>
                                    </Select>
                                    <TextField
                                        name="ipAddress"
                                        label="IP Address"
                                        id="ipAddress"
                                        size="small"
                                        onChange={handleChange}
                                        InputProps={{
                                            style: {
                                                backgroundColor: 'white',
                                            },
                                        }}
                                        style={{ marginTop: "10px" }}
                                    />
                                    <TextField
                                        type="number"
                                        name="port"
                                        label=""
                                        id="port"
                                        size="small"
                                        onChange={handleChange}
                                        InputProps={{
                                            style: {
                                                backgroundColor: 'white',
                                            },
                                        }}
                                        style={{ marginTop: "10px" }}
                                    />
                                    <Button type="submit" variant="contained" style={{ marginTop: "15px", width: "100%", fontSize: "1.1em", backgroundColor: "silver", color: "black" }}></Button>
                                </FormControl>
                            </form>
                        )}
                        {error && (
                            //      .
                            <div style={{ color: 'red' }}> : {error.message}</div>
                        )}
                    </Grid>
                </Grid>
            </Box>
        </Modal>
    );
};

export default ConnectInfoModal;
